<template>
    <div class="talentsDetail">
        <div class="container">
          <div class="banner">
              <div class="person">
                <p class="avat"><img src="../assets/img/avat.png" alt=""></p>
                <p class="name">丑纪范</p>
                <p class="personalText">丑纪范，男，汉族，1934年7月出生，中国科学院院士，博士生导师，享受政府特殊津贴专家。</p>
              </div>
          </div>
          <div class="personalKills">
            <ul>
              <li v-for="(item,index) in killsList" :key="index">
                <p class="title">{{item.title}}</p>
                <p class="text">{{item.text}}</p>
              </li>
            </ul>
          </div>
        </div>
    </div>
</template>
<script>
export default {
  data(){
    return{
      killsList:[
        {
          title:'学习经历',
          text:'1956 年毕业于北京大学， 1981 年 2 月至 1982 年 2 月在美国麻省理工学院进修和工作（访问学者）；历任中国气象学会理事、常务理事、国家自然科学基金委员会地球科学部学科评审组成员。'
        },{
          title:'教学及指导研究生情况',
          text:'培养出一批优秀研究生，其中有２名分别被国家教委和国务院学院委员会授予 “ 做出突出贡献的中国博士学位获得者 ” 称号和 “ 做出突出贡献的中国硕士学位获得者 ” 称号；２名获 “ 涂长望全国青年气象科技一等奖 ” （1992 ； 1998）；指导的１篇博士论文被评为 “ 首届全国优秀博士学位论文 ” （ 1999 ）。'
        },{
          title:'发表论文及专著',
          text:'国内外学术刊物上发表论文 100 余篇，出版专著 7 部。代表性专著有 : 《长期数值天气预报》（获全国高校优秀教材奖），《大气动力学的新进展》，（获国家教委高校出版社优秀学术专著奖），《大气科学中数学方法的应用》，《非线性动力学》等。'
        },{
          title:'研究方向',
          text:'丑纪范院士长期从事数值天气预报、数值模拟以及气候动力学研究和教学，重点研究长期数值天气预报的基础理论和方法以及有关的大气和海洋动力学，特别是非线性动力学问题。他早期将泛函分析引入数值天气预报问题，提出了 “ 广义解 ” 的概念，设计了一种使用多时刻资料的短期数值预报模式；在长期数值预报理论研究中，证明了大气温压场演变与下垫面热状况的等价性，为解决长期数值预报的初值问题奠定了理论基础，进而建立了一个动力统计的季节预报模式；他还在强迫耗散的非线性大气动力学研究中获得了一系列富于创新的成果，有许多工作是开创性的。'
        },
        ]
    }
  }
}
</script>
<style lang="less" >
.talentsDetail{
  .banner{
    background: url("../assets/img/personalBg.png");
    height: 266px;
    width: 100%;
    background-size: 100% 100%;
    .person{
      overflow: hidden;
      padding-top: 1px;
      text-align: left;
      margin:0 140px;
      .avat{
        display: inline-block;
        float: left;
        margin-top: 50px;
        margin-right: 20px;
        img{
          width: 169px;
          height: 207px;
        }
      }
      .name{
        margin-top:120px;
        font-size: 28px;
      }
      .personalText{
        margin-top: 10px;
      }
    }
  }
    .personalKills{
      background: url("../assets/img/newContentBg.jpg");
      padding: 40px 140px;
      text-align: left;
      ul{
        overflow: hidden;
        li{
          .title{
            padding: 10px 0;
            border-bottom:1px solid #10bbb8;
          }
          .text{
            padding: 10px 28px;
          }
        }
      }
    }
}
</style>


